body {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "GothamPro", "Arial", sans-serif;
  background-color: #ffffff;
  color: #000000;
  overflow-x: hidden;
  position: relative; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

@font-face {
  font-family: "GothamPro";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("GothamPro"), local("GothamPro"), url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff"); }

@font-face {
  font-family: "GothamPro";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("GothamPro Bold"), local("GothamPro-Bold"), url("../fonts/GothamPro-Bold.woff2") format("woff2"), url("../fonts/GothamPro-Bold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.hero {
  position: relative;
  min-height: 100vh;
  width: 100%;
  padding: 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  flex-direction: column; }

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #b31419; }
  .bg::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 70%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1); }

.content {
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1400px;
  display: flex;
  justify-content: center;
  align-items: center; }

.textBox {
  position: relative;
  width: 50%;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .textBox__title {
    font-size: 130px;
    line-height: 130px;
    margin-bottom: 20px; }
  .textBox__text {
    font-size: 24px;
    line-height: 24px;
    position: relative;
    padding: 0 20px;
    margin-bottom: 30px; }
    .textBox__text::before, .textBox__text::after {
      position: absolute;
      content: "";
      width: 8px;
      height: 8px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: #ffffff;
      border-radius: 50%; }
    .textBox__text::after {
      left: unset;
      right: 0; }
  .textBox__link {
    padding: 8px 25px;
    color: #ffffff !important;
    background-color: transparent;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    border: 2px solid #ffffff;
    border-radius: 4px;
    font-weight: 700;
    font-family: "GothamPro", "Arial", sans-serif;
    font-size: 16px !important;
    transition: all 0.3s ease-in-out;
    display: block;
    align-self: center;
    position: relative;
    overflow: hidden;
    margin-bottom: 0; }
    .textBox__link::after {
      position: absolute;
      content: "";
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.4) 90%, transparent);
      transition: all 0.3s ease-in-out; }
    .textBox__link:hover::after {
      left: 100%; }

.imgBox {
  position: relative;
  z-index: 1;
  flex-shrink: 1;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .imgBox img {
    max-width: 400px;
    max-height: 400px; }
  .imgBox__price {
    font-size: 30px;
    line-height: 30px;
    text-transform: uppercase;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.4);
    margin-bottom: 20px;
    align-self: flex-end; }
    .imgBox__price .num {
      color: rgba(255, 255, 255, 0.8);
      font-size: 40px;
      line-height: 40px; }

.thumb {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  display: flex; }
  .thumb li {
    padding: 5px;
    margin: 0 20px 0 0;
    cursor: pointer;
    transition: 0.5s;
    background-color: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(255, 255, 255, 0.25);
    display: flex;
    justify-content: center;
    align-items: center; }
    .thumb li:last-child {
      margin: 0; }
    .thumb li::before {
      position: absolute;
      content: attr(data-text);
      top: -80px;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;
      font-size: 70px;
      color: rgba(0, 0, 0, 0.1);
      text-transform: uppercase;
      font-family: "GothamPro", "Arial", sans-serif;
      font-weight: 700;
      opacity: 0;
      pointer-events: none; }
    .thumb li.active {
      border: 2px solid white; }
      .thumb li.active::before {
        opacity: 1; }
  .thumb img {
    max-width: 60px; }
